
@import url(https://fonts.googleapis.com/css?family=EB+Garamond);
@import url(https://fonts.googleapis.com/css?family=Pompiere);
@import url(https://fonts.googleapis.com/css?family=Cabin+Sketch);
@import url(https://fonts.googleapis.com/css?family=Special+Elite);
@import url(https://fonts.googleapis.com/css?family=Patrick+Hand+SC);
@import url(https://fonts.googleapis.com/css?family=Forum);
#ThemesSlideshow h3 {
  font-weight: normal;
  text-transform: uppercase;
}
#ThemesSlideshow .eg-slide-content {
  width: 25em;
  max-width: 80%;
  margin: 0 auto;
}
#ThemesSlideshow .local-eg-theme-artic {
  background-color: #11a9e2;
  background-image: none;
  color: #fff;
}
#ThemesSlideshow .local-eg-theme-artic h1 {
    font-weight: bold;
    font-size: 3em;
}
#ThemesSlideshow .local-eg-theme-artic h1, #ThemesSlideshow .local-eg-theme-artic p {
    font-family: "Comfortaa", Helvetica, Arial, sans-serif;
    margin-top: 0;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translateX(-50%) translateY(-50%);
    width: 80%;
    text-shadow: 0 0.01em 0 #ccc, 0 0.02em 0 #c9c9c9, 0 0.03em 0 #bbb, 0 0.04em 0 #b9b9b9, 0 0.05em 0 #aaa, 0 0.06em 0.01em rgba(0, 0, 0, 0.1), 0 0 0.05em rgba(0, 0, 0, 0.1), 0 0.01em 0.03em rgba(0, 0, 0, 0.3), 0 0.03em 0.05em rgba(0, 0, 0, 0.2), 0 0.05em 0.1em rgba(0, 0, 0, 0.25), 0 0.1em 0.1em rgba(0, 0, 0, 0.2), 0 0.2em 0.2em rgba(0, 0, 0, 0.15);
}
#ThemesSlideshow .local-eg-theme-foreword {
  background: url("./assets/pw_maze_black.png") repeat;
  font-family: 'EB Garamond';
  color: #ccc;
}
#ThemesSlideshow .local-eg-theme-foreword .eg-slide-content {
    max-width: 90%;
    width: 15em;
}
#ThemesSlideshow .local-eg-theme-foreword p {
    margin-top: 30%;
}
#ThemesSlideshow .local-eg-theme-bretagne {
  background-image: url(http://i.imgur.com/rYkJ6I8.jpg);
  background-position: center;
  background-size: cover;
}
#ThemesSlideshow .local-eg-theme-bretagne h3 {
    font-family: 'Pompiere';
    text-transform: none;
    font-size: 3em;
    color: rgba(255, 255, 255, 0.6);
}
#ThemesSlideshow .local-eg-theme-forest-road {
  background-image: url(http://i.imgur.com/hxTMFZW.jpg);
  background-position: center;
  background-size: cover;
}
#ThemesSlideshow .local-eg-theme-forest-road h3 {
    font-family: 'Cabin Sketch';
    margin-top: 50%;
    text-align: left;
    color: rgba(255, 255, 255, 0.9);
}
#ThemesSlideshow .local-eg-theme-spark {
  background-image: url(http://i.imgur.com/FL9mwpd.jpg);
  background-position: center;
  background-size: cover;
}
#ThemesSlideshow .local-eg-theme-spark h3 {
    position: absolute;
    font-family: 'Special Elite';
    color: rgba(255, 255, 255, 0.8);
    font-size: 1em;
    bottom: 10%;
    float: left;
}
#ThemesSlideshow .local-eg-theme-city {
  /*

Monokai Sublime style. Derived from Monokai by noformnocontent http://nn.mit-license.org/

*/
  background-image: url(http://i.imgur.com/kmmHith.jpg);
  background-position: center;
  background-size: cover;
}
#ThemesSlideshow .local-eg-theme-city .hljs {
    display: block;
    overflow-x: auto;
    padding: 0.5em;
    background: #23241f;
}
#ThemesSlideshow .local-eg-theme-city .hljs,
  #ThemesSlideshow .local-eg-theme-city .hljs-tag,
  #ThemesSlideshow .local-eg-theme-city .hljs-subst {
    color: #f8f8f2;
}
#ThemesSlideshow .local-eg-theme-city .hljs-strong,
  #ThemesSlideshow .local-eg-theme-city .hljs-emphasis {
    color: #a8a8a2;
}
#ThemesSlideshow .local-eg-theme-city .hljs-bullet,
  #ThemesSlideshow .local-eg-theme-city .hljs-quote,
  #ThemesSlideshow .local-eg-theme-city .hljs-number,
  #ThemesSlideshow .local-eg-theme-city .hljs-regexp,
  #ThemesSlideshow .local-eg-theme-city .hljs-literal,
  #ThemesSlideshow .local-eg-theme-city .hljs-link {
    color: #ae81ff;
}
#ThemesSlideshow .local-eg-theme-city .hljs-code,
  #ThemesSlideshow .local-eg-theme-city .hljs-title,
  #ThemesSlideshow .local-eg-theme-city .hljs-section,
  #ThemesSlideshow .local-eg-theme-city .hljs-selector-class {
    color: #a6e22e;
}
#ThemesSlideshow .local-eg-theme-city .hljs-strong {
    font-weight: bold;
}
#ThemesSlideshow .local-eg-theme-city .hljs-emphasis {
    font-style: italic;
}
#ThemesSlideshow .local-eg-theme-city .hljs-keyword,
  #ThemesSlideshow .local-eg-theme-city .hljs-selector-tag,
  #ThemesSlideshow .local-eg-theme-city .hljs-name,
  #ThemesSlideshow .local-eg-theme-city .hljs-attr {
    color: #f92672;
}
#ThemesSlideshow .local-eg-theme-city .hljs-symbol,
  #ThemesSlideshow .local-eg-theme-city .hljs-attribute {
    color: #66d9ef;
}
#ThemesSlideshow .local-eg-theme-city .hljs-params,
  #ThemesSlideshow .local-eg-theme-city .hljs-class .hljs-title {
    color: #f8f8f2;
}
#ThemesSlideshow .local-eg-theme-city .hljs-string,
  #ThemesSlideshow .local-eg-theme-city .hljs-type,
  #ThemesSlideshow .local-eg-theme-city .hljs-built_in,
  #ThemesSlideshow .local-eg-theme-city .hljs-builtin-name,
  #ThemesSlideshow .local-eg-theme-city .hljs-selector-id,
  #ThemesSlideshow .local-eg-theme-city .hljs-selector-attr,
  #ThemesSlideshow .local-eg-theme-city .hljs-selector-pseudo,
  #ThemesSlideshow .local-eg-theme-city .hljs-addition,
  #ThemesSlideshow .local-eg-theme-city .hljs-variable,
  #ThemesSlideshow .local-eg-theme-city .hljs-template-variable {
    color: #e6db74;
}
#ThemesSlideshow .local-eg-theme-city .hljs-comment,
  #ThemesSlideshow .local-eg-theme-city .hljs-deletion,
  #ThemesSlideshow .local-eg-theme-city .hljs-meta {
    color: #75715e;
}
#ThemesSlideshow .local-eg-theme-city .eg-code-block {
    margin-top: 30%;
}
#ThemesSlideshow .local-eg-theme-city .code-box {
    box-shadow: 0 0 0.25em 0.25em #ddd;
}
#ThemesSlideshow .local-eg-theme-space {
  background-image: url(http://i.imgur.com/yO2ivoD.jpg);
  background-position: center;
  background-size: cover;
}
#ThemesSlideshow .local-eg-theme-space h3 {
    font-family: 'Forum';
    font-size: 1.5em;
    color: rgba(255, 255, 255, 0.75);
    letter-spacing: 1.5px;
    margin-top: 50%;
}



